import config from "@/config/common.config"
import { setCookie, downLoadLink, storage } from 'ais-common-utils'
import avatar from '@/assets/images/common/avator.png'
export default {
  data () {
    return {
      tabList: [
        {icon: require('@/assets/images/account/tab1.png'), path: '/studyCenter/lastStudy', text: '最近学习'},
        {icon: require('@/assets/images/account/tab2.png'), path: '/studyCenter/myCourse', text: '我的课程'},
        {icon: require('@/assets/images/account/tab3.png'), path: '/studyCenter/myOrder', text: '课程订单'},
        {icon: require('@/assets/images/account/tab6.png'), path: '/studyCenter/biuniqueOrder', text: '服务订单'},
        {icon: require('@/assets/images/account/shouyi.png'), path: '/pushSell', text: '推广收益'},
        {icon: require('@/assets/images/account/tab5.png'), path: `${config.LINK.ais_h5}/personal/coupon`, text: '我的优惠劵'},
        {icon: require('@/assets/images/account/tab4.png'), path: '/studyCenter/myFavor', text: '我的收藏'},
      ],
      avatar,
      config
    }
  },
  mounted() {
    this.$store.dispatch('user/getUserInfoHand')
  },
  methods: {
    // 退出登录
    async logOut () {
      await this.$store.dispatch('user/signOut')
      localStorage.removeItem('isAgree')
      // 设置用户自动退出cookie
      setCookie('userSignOut', '1')
    },
    // 登录
    loginIn () {
      if(storage.get('userStatus')){
        //登录前先把store的userInfo清空，重新拉取一次auth接口
        this.$store.commit('user/setUserInfo', {})
        this.$store.dispatch('user/getUserInfoHand')
      }else{
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${window.location.href}`
      }
    },
    // 菜单链接跳转
    linkToPath (path) {
      if (!this.$store.state.user.userInfo.userStatus)return this.$toast('请先登录')
      path.path.indexOf(config.LINK.ais_h5) > -1 ? downLoadLink(path.path) : this.$router.push(path)
    }
  },
  head () {
    return {
      title: '我的 -艾思云课堂'
    }
  }
}
